/*
Fonts
*/
h1 {
  font-size: 3.5rem;
  font-family: 'Montserrat', sans-serif; }

/*
Variables
*/
html {
  height: 100%;
  font-size: 14px; }

body {
  height: 100%;
  margin: 0;
  padding: 0;
  background-color: #000; }

#root {
  color: #4d4d4d;
  margin: 0; }

.background {
  background: url(../assets/img/background2.jpg) top;
  background-size: cover;
  position: absolute;
  display: block;
  width: 100%;
  height: 100vh; }
  .background::after {
    content: '';
    position: absolute;
    top: 0;
    bottom: 0;
    width: 100%;
    background: -webkit-gradient(linear, left bottom, left top, color-stop(35%, rgba(0, 0, 0, 0)), color-stop(90%, rgba(0, 0, 0, 0.7)), to(#000));
    background: -webkit-linear-gradient(bottom, rgba(0, 0, 0, 0) 35%, rgba(0, 0, 0, 0.7) 90%, #000 100%);
    background: -o-linear-gradient(bottom, rgba(0, 0, 0, 0) 35%, rgba(0, 0, 0, 0.7) 90%, #000 100%);
