/*
Suggestions
*/
.suggestions {
  width: 100%;
  height: auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  padding: 20px 40px;
  font-family: "Raleway", sans-serif;
  font-size: 1rem;
  line-height: 1.4rem;
  /*
  Slide Up Effect
  */ }
  .suggestions::after {
    content: '';
    position: absolute;
    width: 100%;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: -webkit-gradient(linear, left top, left bottom, color-stop(10%, rgba(0, 0, 0, 0)), color-stop(20%, #000));
    background: -webkit-linear-gradient(top, rgba(0, 0, 0, 0) 10%, #000 20%);
    background: -o-linear-gradient(top, rgba(0, 0, 0, 0) 10%, #000 20%);
    background: linear-gradient(to bottom, rgba(0, 0, 0, 0) 10%, #000 20%);
    z-index: -1; }
  .suggestions--card {
    display: inline-block;
    width: calc(100% * (1/4) - 10px - 10px);
    height: auto;
    background-color: #FFF;
    margin: 10px;
    padding: 20px;
    opacity: 0;
    -webkit-animation-name: slideUp, fadeIn;
            animation-name: slideUp, fadeIn;
    -webkit-animation-duration: 1.5s, 0.75s;
            animation-duration: 1.5s, 0.75s;
    -webkit-animation-delay: .9s;
            animation-delay: .9s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    -webkit-animation-timing-function: cubic-bezier(0.19, 1, 0.22, 1);
            animation-timing-function: cubic-bezier(0.19, 1, 0.22, 1);
    visibility: visible !important; }
    .suggestions--card:nth-child(1) {
      -webkit-animation-delay: 0.1s;
              animation-delay: 0.1s; }
    .suggestions--card:nth-child(2) {
      -webkit-animation-delay: 0.2s;
              animation-delay: 0.2s; }
    .suggestions--card:nth-child(3) {
      -webkit-animation-delay: 0.3s;
              animation-delay: 0.3s; }
    .suggestions--card:nth-child(4) {
      -webkit-animation-delay: 0.4s;
              animation-delay: 0.4s; }
    .suggestions--card:nth-child(5) {
      -webkit-animation-delay: 0.5s;
              animation-delay: 0.5s; }
    .suggestions--card:nth-child(6) {
      -webkit-animation-delay: 0.6s;
              animation-delay: 0.6s; }
    .suggestions--card:nth-child(7) {
      -webkit-animation-delay: 0.7s;
              animation-delay: 0.7s; }
    .suggestions--card:nth-child(8) {
      -webkit-animation-delay: 0.8s;
              animation-delay: 0.8s; }
    .suggestions--card--content--section {
      margin-bottom: 10px;
      padding-bottom: 10px;
      border-bottom: 1px solid #dcdcdc; }
      .suggestions--card--content--section:last-of-type {
        border: none;
        padding-bottom: 0; }
    .suggestions--card--content--title {
      font-weight: bold; }
    .suggestions--card p {
      margin-bottom: 0;
      overflow: hidden;
      white-space: nowrap;
      -o-text-overflow: ellipsis;
         text-overflow: ellipsis; }
    .suggestions--card--header {
      display: -ms-flexbox;
      display: flex;
      -ms-flex-wrap: wrap;
          flex-wrap: wrap;
      margin-bottom: 15px; }
      .suggestions--card--header p {
        -ms-flex-positive: 1;
            flex-grow: 1;
        padding-bottom: 10px;
        text-transform: uppercase;
        border-bottom: 1px solid #dcdcdc;
        font-weight: bold;
        color: #a9a9a9; }
        .suggestions--card--header p:last-of-type, .suggestions--card--header p a {
          text-align: right;
          text-transform: lowercase;
          color: #cd5c5c; }

@-webkit-keyframes slideUp {
  0% {
    -webkit-transform: translateY(100%);
            transform: translateY(100%); }
  100% {
    -webkit-transform: translateY(0%);
            transform: translateY(0%); } }

@keyframes slideUp {
  0% {
    -webkit-transform: translateY(100%);
            transform: translateY(100%); }
  100% {
    -webkit-transform: translateY(0%);
            transform: translateY(0%); } }

@-webkit-keyframes fadeIn {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@keyframes fadeIn {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }



